@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@media only screen and (max-width: 768px) {
  div.app {
    grid-template-rows: 16vw 1fr 20vw;
  }

  div.header {
    height: 16vw;
  }

  div.title {
    font-size: 5vw;
  }

  a.route-button {
    height: 80%;
    width: 40vw;
    padding: 0 2vw 0 2vw;
    border-radius: 3vw;
  }
  
  a.route-button > img {
    height: 80%;
  }

  div.header-text {
    font-size: 5vw;
  }

  div.call {
    font-size: 5vw;
  }

  footer.footer {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
  }

  a.about-button {
    text-align: center;
    margin-right: 0;
    margin-bottom: 2vw;
    justify-content: center;
    font-size: 4vw;
    width: 100%;
  }

  a.about-button:hover {
    font-size: 4.1vw;
  }

  footer.footer {
    font-size: 4vw;
  }
}
:root {
  --default-color: #c3352b;
  --darker-color: #922820;
  --lighter-color: #f44336;
}

* {
  font-family: 'Montserrat', sans-serif;
  outline: none;
}

::-webkit-input-placeholder {
  color: #fff;
}

.app {
  height: 100vh;
  display: grid;
  grid-template-rows: 6vw 1fr 3vw;
  grid-template-columns: 1fr;
  grid-template-areas: 
    "header"
    "content"
    "footer";
}

.content {
  grid-row: content;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0.8vw;
  grid-row: header;
  background-color: var(--default-color);
  color: white;
}

.link-logo {
  height: 100%;
}

.avatar {
  border-radius: 50%;
  height: 100%;
}

.logo {
  height: 100%;
}

.title {
  margin-left: 2vw;
  padding: 0.2vw;
  font-size: 4vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 22vw;
  height: 100%;
  text-decoration: none;
  color: white;
  -webkit-transition: padding 150ms linear 0s;
  transition: padding 150ms linear 0s;
}

.title:hover {
  font-weight: bold;
  padding: 0;
}

a:hover {
  text-decoration: none;
}

.header-text {
  margin-left: 0.6vw;
  font-size: 1.5vw;
  color: white;
  text-decoration: none;
  text-align: center;
}

.route-button {
  display: flex;
  flex-direction: row;
  padding: 0.7vw;
  justify-content: space-between;
  align-items: center;
  margin-right: 0.7vw;
  width: 12vw;
  height: 100%;
  border: 0.1rem solid var(--darker-color);
  border-radius: 1vw;
  box-shadow: 0px 0px 7px -4px rgba(0,0,0,0.75);
  -webkit-transition: background-color 150ms linear 0s;
  transition: background-color 150ms linear 0s;
}

.route-button:hover {
  background-color: var(--darker-color);
  text-decoration: none;
}

.call {
  font-size: 3.7vw;
  color: #ffa600;
  font-weight: bold;
}
.footer {
  display: flex;
  font-size: 1vw;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.8vw;
  background-color: var(--darker-color);
  color: white;
  box-shadow: var(--shadow);
  grid-row: footer;
  white-space: nowrap;
}

.about-button {
  display: flex;
  flex-direction: row;
  padding: 0.7vw;
  color: white;
  justify-content: center;
  align-items: center;
  margin-left: 0.5vw;
  margin-right: 0.7vw;
  width: 22vw;
  white-space: nowrap;
}

.about-button:hover {
  text-decoration: none;
  font-size: 1.03vw;
  color: white;
  font-weight: bolder;
}

.github-link {
  text-decoration: none;
  color: white;
}

.github-link:hover {
  color: #ccc;
}


.about-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: -webkit-gradient(linear, left top, right top, from(#fc4a1a),to(#f7b733) );
  background: linear-gradient(to right, #fc4a1a 0%,#f7b733 100% );
  box-shadow: 0px 10px 42px -13px rgba(0,0,0,0.75);
}

.about-header > img {
  max-width: 47vw;
  max-height: 14.3vw;
}

.about-header > h1 {
  margin-right: 18vw;
  font-size: 4vw;
  font-weight: bolder;
  color: white;
}
@media only screen and (max-width: 600px) {
  div.book-card {
    font-size: 8vw;
    margin-top: 6vw;
    border-radius: 0;
    width: 100%;
    height: 45vw;
    box-shadow: none;
    border-top: solid 0.5vw #ddd;
    border-bottom: solid 0.5vw #ddd;

    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }

  div.book-card:hover {
    box-shadow: none;
  }

  div.thumbnail {
    margin-left: 2vw;
    width: 45%;
    height: 100%;
  }

  div.thumbnail > img {
    max-width: 70%;
    height: 100%;
  }

  div.loading-thumbnail {
    width: 60%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  div.book-information {
    width: 67%;
    height: 100%;
    justify-content: center;
    margin-right: 4vw;
  }

  div.book-description {
    height: 13vw;
  }

  div.book-description > span {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3.5vw;
    white-space: nowrap;
  }

  div.book-description > p {
    font-size: 3vw;
    margin-bottom: 0;
    text-align: center;
  }

  div.user-information {
    font-size: 3vw;
  } 

  button.card-button {
    margin-top: 5vw;
    font-size: 3vw;
    width: 25vw;
    height: 8vw;
  }
}
@media only screen and (max-width: 768px) {
  div.books-header {
    box-shadow: 0px 8px 25px -12px rgba(0,0,0,0.75);
  }

  div.books {
    display: grid;
    grid-template-rows: 50vw 1fr;
    grid-template-areas: 
      'header'
      'gallery';
  }

  div.search-section > h1 {
    font-size: 20vw;
  }

  div.search-bar {
    font-size: 4vw;
    width: 70vw;
    height: 10vw;
    padding: 0 6vw 0 2vw;
    border-radius: 5vw;
  }

  div.search-bar > input {
    margin-left: 2vw;
    height: 10vw;
  }

  div.books-gallery {
    padding: 0;
    display: flex;
    flex-direction: column;
  }

  img.books-image {
    display: none;
  }

  div.thumbnail > img {
    width: 100%;
    height: 70%;
  }

  div.books-footer {
    margin-top: 5vw;
    margin-bottom: 3vw;
    align-items: center;
    justify-content: center;
  }

  div.books-footer > button {
    margin: 0 4vw 0 4vw;
    width: 20vw;
    height: 10vw;
    font-size: 3vw;
  }

  div.loading-gallery {
    height: 80vw;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  div.loading-gallery > i {
    font-size: 14vw;
  }

  div.loading-gallery > p {
    width: 100%;
    margin-top: 2vw;
    font-size: 5vw;
    text-align: center;
  }

  div.book-not-found {
    margin-top: 10vw;
    height: 60vw;
    font-size: 7vw;
  }
  
  div.book-not-found > i {
    font-size: 14vw;
    margin-bottom: 2vw;
  }
}
.book-card {
  display: flex;
  flex-direction: column;
  align-items: center;

  height: 26vw;
  margin: 4vw auto auto auto;
  width: 20vw;
  border-radius: 1.5vw;
  box-shadow: 0px 0px 14px -4px rgba(0,0,0,0.75);
  -webkit-transition: box-shadow 150ms linear 0s;
  transition: box-shadow 150ms linear 0s;
}

.book-card:hover {
  box-shadow: 0px 0px 24px 0px rgba(0,0,0,0.75);
}

.thumbnail {
  margin-top: 2vw;
  margin-bottom: 0.8vw;
  height: 35%;
  max-width: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.thumbnail > img {
  max-height: 100%;
  max-width: 100%;
}

.loading-thumbnail {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading {
  font-size: 5vw;
  color: var(--default-color);

  -webkit-animation-name: rotating;

          animation-name: rotating;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  } to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  } to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.book-information {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.book-description {
  text-align: center;
}

.book-description > span {
  margin-top: 1vw;
  font-weight: bolder;
  font-size: 1vw;
}

.book-description > p {
  font-size: 0.8vw;
}

.user {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 0.6vw;
  background-color: #dedede;
  width: 100%;
}

.user-information {
  color: #666;
  font-size: 1vw;
  overflow: hidden;
  margin-bottom: 0.7vw;
}

.user-information > span {
  margin-left: 0.5vw;
}

.card-button {
  margin-top: 1vw;
  margin-bottom: 1vw;
  font-size: 1.2vw;
  width: 8vw;
  height: 3vw;
  background-color: var(--lighter-color);
  border: none;
  color: white;
  border-radius: 1.5vw;
}

.card-button:hover {
  background-color: var(--darker-color);
}
.books {
  display: grid;
  grid-template-rows: 20vw 1fr;
  grid-template-areas: 
    'header'
    'gallery';
}

.books-header {
  grid-area: header;
  height: 100%;
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: 'image1 search image2';
  box-shadow: 0px 16px 55px -25px rgba(0,0,0,0.75);
}

.image1 {
  grid-area: image1;
}

.image2 {
  grid-area: image2;
}

.search-section {
  grid-area: search;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.books-image {
  width: 100%;
  height: 100%;
}

.search-section > h1 {
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: 4vw;
  color: var(--default-color);
  font-weight: bold;
}

.search-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 25vw;
  height: 2.7vw;
  padding: 0 1.4vw 0 1vw;
  color: white;
  font-size: 1.2vw;

  background-color: var(--default-color);
  border-radius: 3vw;
}

.search-bar > i {
  color: white;
  margin-right: 0.4vw;
}

.search-bar > input {
  background-color: var(--default-color);
  border: none;
  color: white;
  width: 93%;
  height: 2.7vw;
}

.books-gallery {
  grid-area: gallery;
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: 'c1' 'c2' 'c3';
  padding: 0 5vw 5vw 5vw;
}

.books-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0 1vw 1vw 0;
}

.books-footer > button {
  background-color: white;
  border: solid 0.15vw var(--default-color);
  font-size: 1vw;
  display: flex;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  height: 3vw;
  width: 7vw;
  margin: 0 1vw;
  border-radius: 0.4vw;
}

.books-footer > button:hover {
  background-color: #ddd;
}

.loading-gallery {
  height: 20vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.loading-gallery > p {
  margin-top: 2vw;
  font-size: 1vw;
}

.book-not-found {
  margin-top: 3vw;
  height: 30vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 2vw;
  color: var(--default-color);
  font-weight: bolder;
  margin: auto;
}

.book-not-found > i {
  font-size: 7vw;
  margin-bottom: 2vw;
}
@media only screen and (max-width: 768px) {
  div.form-container {
    margin: 5vw 0 10vw 0;
    height: 100%;
    width: 100%;
    border-radius: 0;
    box-shadow: none;
  }

  form.form > img {
    border-width: 0.6vw;
    height: 30vw;
    width: 30vw;
    margin-bottom: 0.4vw;
  }

  form.form > h1 {
    font-size: 7vw;
    margin-bottom: 10vw;
  }

  form.form > p {
    width: 80%;
    font-size: 4vw;
  }

  form.form input {
    margin-bottom: 4vw;
    border-width: 0.4vw;
    border-radius: 1.3vw;
    padding: 1.5vw;
    height: 10vw;
    width: 80%;
    font-size: 3.6vw;
  }

  form.form > button {
    width: 80%;
    height: 10vw;
    font-size: 5vw;
    border-radius: 1.3vw;
  }

  form.form > hr {
    width: 80%;
  }

  form.form > a {
    margin-top: 5vw;
    font-size: 5vw;
  }
}
.form-container {
  margin: 3vw auto 3vw auto;
  height: 100%;
  padding: 2vw;
  width: 50vw;
  box-shadow: 0px 0px 29px -9px rgba(0,0,0,0.75);
  border-radius: 2vw;
}

.form {
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form > img {
  border-radius: 50%;
  border: solid 0.1vw #ddd;
  height: 15vw;
  width: 15vw;
  margin-bottom: 0.4vw;
}

.form > h1 {
  margin-bottom: 4vw;
  color: var(--default-color);
  font-weight: bolder;
  font-size: 3vw;
}

.form > p {
  color: #db8400;
  font-size: 1.1vw;
  margin-bottom: 15px;
  border: 1px solid #db8400;
  padding: 10px;
  width: 30vw;
  text-align: center;
}

.form input {
  margin-bottom: 2vw;
  border: solid 0.1vw var(--default-color);
  border-radius: 0.5vw;
  padding: 1vw;
  height: 4vw;
  width: 30vw;
  font-size: 1.3vw;
}

.form input::-webkit-input-placeholder {
  color: #999;
}

.form input:-ms-input-placeholder {
  color: #999;
}

.form input::-ms-input-placeholder {
  color: #999;
}

.form input::placeholder {
  color: #999;
}

.form > button {
  width: 30vw;
  height: 4vw;
  background-color: var(--default-color);
  border: none;
  border-radius: 0.5vw;
  color: white;
  font-size: 1.6vw;
}

.form > button:hover {
  background-color: var(--darker-color);
}

.form > hr {
  margin: 4vw 0 2vw 0;
  border-bottom: solid 0.1vw #999;
  width: 30vw;
}

.form > a {
  font-size: 1.6vw;
  font-weight: bolder;
  color: var(--default-color);
}

.form > a:hover {
  color: var(--darker-color);
}
